import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  title: string = new Date().toLocaleTimeString();
  bShow: boolean = false;
  age: number = 100;
  picUrl: string = "http://lorempixel.com/400/200/";
  v : string;

  Constructor(){
  }

  bClicked(){
    setInterval(()=> {
        this.bShow = !this.bShow;
        this.age++;
        if ((this.age % 2) != 0 ){
          this.picUrl = "http://lorempixel.com/400/200/?";
        }
        else {
          this.picUrl = "http://lorempixel.com/400/200";
        }
        
        this.visib();
    }, 2000);
  }

  valChanged(){
    this.age = 0;
  }

  visib(){
    if ((this.age % 2) != 0 ){
      this.v = "red";
    }
    else {
      this.v = "green";
    }
  }
}



